import { useState } from "react";
import cl from "./DualInputDropdown.module.css"
import DropdownMenu from "../dropdownMenu/DropdownMenu";
import { DownOutlined } from '@ant-design/icons';
import { Menu, Dropdown, Button } from 'antd';
import { HeadlineBody } from "../../../textStyles/TextStyleComponents";


const createMenu = (options, onSelect) => {
    // Фильтруем опции, если оставшееся время меньше 2 часов

    // Создаем меню на основе отфильтрованных опций
    return (
        <Menu onClick={onSelect} className={cl.customMenu}>
            {options.map((option, index) => (
                <Menu.Item key={index} className={cl.customMenuItem}>
                    {option}
                </Menu.Item>
            ))}
        </Menu>
    );
};

const DualInputDropdown = () => {
    const [inputValue1, setInputValue1] = useState('');
    const [inputValue2, setInputValue2] = useState('');

    const [selected, setSelected] = useState("Не напоминать");
    const options = [
        "Не напоминать",
        "Напомнить за 15 минут",
        "Напомнить за 30 минут",
        "Напомнить за 1 час",
        "Напомнить за 2 часа",
        "Напомнить за 6 часов",
        "Напомнить за 1 сутки",
    ];

    const handleMenuClick = (e) => {
        setSelected(options[e.key]);
    };

    const menu = createMenu(options, handleMenuClick);

    return (
        <div className={cl.container}>
            <div className={cl.inputWrapper}>
                <input
                    type="text"
                    value={inputValue1}
                    onChange={(e) => setInputValue1(e.target.value)}
                    placeholder="Имя"
                    className={cl.inpt}
                />
                <div className={cl.borderBottom}></div>
            </div>
            <div className={cl.inputWrapper}>
                <input
                    type="text"
                    value={inputValue2}
                    onChange={(e) => setInputValue2(e.target.value)}
                    placeholder="Номер телефона"
                    className={cl.inpt}
                />
                <div className={cl.borderBottom}></div>
            </div>

            <Dropdown overlay={menu} className={cl.drpdwn}>
                <Button className={cl.drpdwnBtn}>
                    <HeadlineBody>{selected}</HeadlineBody> 
                    <DownOutlined />
                </Button>
            </Dropdown>

        </div>
    )
}

export default DualInputDropdown
